body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

/*CORE SECTION*/
:root {
    --white-base : #E4E4E4;
    --black-base : #3C3B49;
}



html {
    scroll-behavior: smooth;
}

.title-black {
    color: #3C3B49;
    color: var(--black-base);
}

*{
    font-family:'Quicksand', sans-serif;
    letter-spacing: 1.5px;
}

.hr-white {
    border-bottom: 2px solid #E4E4E4;
    border-bottom: 2px solid var(--white-base);
}

.links {
    text-decoration: underline;
    color: #3C3B49;
    color: var(--black-base);
}

.links-white {
    text-decoration: underline;
    color: #E4E4E4;
    color: var(--white-base);
}

.links-white:hover {
    color: #3C3B49;
    color: var(--black-base);
}

body {
    padding: 0;
    margin: 0;
    overflow-x: hidden;
    font-family: 'Quicksand', sans-serif;
    background-image: url(/static/media/1.22c536d9.jpg);
    background-size: cover;
    background-attachment: fixed;
}

code {
    background-color: #3C3B49;
    background-color: var(--black-base);
    padding: 4px;
    color:#00ff40 !important;
}

/*SCROLLBAR*/

/* width */
::-webkit-scrollbar {
    width: 12px;
}

::-webkit-scrollbar-track {
    background: #E4E4E4;
    background: var(--white-base);
}

::-webkit-scrollbar-thumb {
    background: #3C3B49;
    background: var(--black-base);
}
  
::-webkit-scrollbar-thumb:hover {
    background: rgb(141, 141, 141);
}

/*NAVBAR SECTION*/

nav { 
    box-shadow: 0px 10px 15px 10px rgb(0 0 0 / 15%);
    background-color: #3C3B49;
    background-color: var(--black-base); 
    border-bottom: 2px solid #E4E4E4; 
    border-bottom: 2px solid var(--white-base);
    color: #E4E4E4;
    color: var(--white-base);
    text-decoration: none;
}


.navbar-brand {
    color: #E4E4E4;
    color: var(--white-base);
    background-image: linear-gradient(
        transparent 0%,
        transparent 90%,
        #E4E4E4 90%,
        #E4E4E4 100%
    );
    background-repeat: no-repeat;
    background-size: 0% 100%;
    background-position-x: right;
    transition: background-size 800ms;
    margin-left: 30px;
}

.navbar-brand:hover {
    cursor: pointer;
    background-size: 100% 100%;
    background-position-x: left;
    color: #E4E4E4;
    color: var(--white-base);
}


.nav-item .nav-link {
    color: #E4E4E4;
    color: var(--white-base);
    text-decoration: none;
    transition: ease-out 0.2s;
    margin: 0px 10px 0px 10px;
    background-image: linear-gradient(
        transparent 0%,
        transparent 90%,
        #E4E4E4 90%,
        #E4E4E4 100%
    );
    background-repeat: no-repeat;
    background-size: 0% 100%;
    background-position-x: right;
    transition: background-size 800ms;

}

.navbar-nav .nav-link:hover {
    color: #E4E4E4;
    color: var(--white-base);
    cursor: pointer;
    background-size: 100% 100%;
    background-position-x: left;
}

.dropdown-menu-toggle {
    width: 25px;
    height: 25px;
    -webkit-filter: invert();
            filter: invert();
}


/*LANDING PAGE SECTION*/

.sect1 {
    height: 70vh;
    margin: 0px 100px 0px 100px;
    text-align: center;
}

.greeting {
    margin-top : 260px;
    color: #E4E4E4;
    color: var(--white-base);
}

/*ABOUT SECTION*/

.about {
    padding: 40px 90px 40px 90px;
    text-align: center;
    background-color:#E4E4E4;
    background-color:var(--white-base); 
}

.img-logo-social {
    height: 25px;
    width: 25px;
    margin-right: 5px;
}

.row-who {
    display: flex;
    align-content: center;
    flex-wrap: nowrap;
}

.column-who {
    padding: 25px;
    margin: 12px;
    font-size: large;
    text-align: center;
    width: 200px;   
}

.column-who:hover {
    background-color: #3C3B49;
    background-color: var(--black-base);
    color: #E4E4E4;
    color: var(--white-base);
    border-radius: 10px;
    cursor: pointer;
    box-shadow: 0px 10px 15px 10px rgb(0 0 0 / 15%);
}

.column-who:hover .img-id {
    -webkit-filter:invert(1.0);
            filter:invert(1.0);
    -webkit-transform: scale(1.2);
            transform: scale(1.2);
}

.column-who:hover .img-id-ina {
    -webkit-transform: scale(1.2);
            transform: scale(1.2);
}

.column-who:hover .links {
    color: #E4E4E4;
    color: var(--white-base);
}

.img-id-ina {
    height: 75px;
    width: 75px;
    transition: 0.2s;
}

.img-id {
    height: 75px;
    width: 75px;
    transition: 0.2s;
}

.roles {
    background-color: #3C3B49;
    background-color: var(--black-base);
    padding: 25px;
    color: #E4E4E4;
    color: var(--white-base);
    border-radius: 10px;
    box-shadow: 0px 10px 15px 10px rgb(0 0 0 / 15%);
}

.column-roles {
    background-color: #3C3B49;
    background-color: var(--black-base);
    color: #E4E4E4;
    color: var(--white-base);
    transition: 0.4s;
    border-radius: 10px;
    cursor: pointer;
    box-shadow: 0px 10px 15px 10px rgb(0 0 0 / 15%);
    padding: 20px;
    margin: 10px;
    border: 2px solid #E4E4E4;
    border: 2px solid var(--white-base);
}

.column-roles:hover {
    -webkit-transform: translateY(-15px);
            transform: translateY(-15px);
    transition: 0.4s;
}

.roles-img {
    -webkit-filter: invert(1.0);
            filter: invert(1.0);
}

/*PROJECT SECTION*/

#projects {
    background-image: url(/static/media/2.8120f97f.jpg);
    background-size: cover;
    background-attachment: scroll;
    padding: 15px;
}

.project {
    margin: 15px;
    padding: 25px;
    text-align: center;
    border-radius: 7px;
    color: #E4E4E4;
    color: var(--white-base);
}

.project h1 {
    color: #3C3B49;
    color: var(--black-base);
}

.row-proj {
    display: flex;
    flex-wrap: wrap;
    align-content: space-between;
    align-items: center;
}

.col-proj {
    margin: 10px auto;
    background-color: #3C3B49;
    background-color: var(--black-base);
    border-radius: 10px;
    padding: 0;
    width: 275px;
    height: 350px;
    transition: 0.4s;
}

.tags-container {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    margin-top: 15px;
    grid-gap: 5px;
    gap: 5px;
}

.tags {
    padding: 7px;
    font-size: 12px;
    color: #E4E4E4;
    color: var(--white-base);
    border: 1px solid #E4E4E4;
    border: 1px solid var(--white-base);
}

.tags:hover {
    cursor: pointer;
    background-color: #E4E4E4;
    background-color: var(--white-base);
    color: #3C3B49;
    color: var(--black-base);
    transition: 0.4s;
}

.img-proj {
    width: 275px;
    height: 155px;
    transition: 0.12s;
    border-radius: 7px;
    margin: 0;
}

.col-proj:hover {
    -webkit-transform: translateY(-15px);
            transform: translateY(-15px);
    outline: 2px solid #E4E4E4;
    outline: 2px solid var(--white-base);
    cursor: pointer;
    box-shadow: 0px 10px 15px 10px rgb(0 0 0 / 15%);
    transition: 0.4s;
}

.img-about {
    height: 450px;
    width: 850px;
}

/*TECH STACK (SKILLS) SECTION*/
.skills-bg {
    background-color: #E4E4E4;
    background-color: var(--white-base);
    padding: 50px 20px 50px 20px;
    text-align: center;
    color: #3C3B49;
    color: var(--black-base);
}

.main-container {
    background-color: #3C3B49;
    background-color: var(--black-base);
    padding: 20px;
    margin: 40px;
    border-radius: 10px;
    color: #E4E4E4;
    color: var(--white-base);
}   

.skills-container {
    display: flex;
    grid-gap: 20px;
    gap: 20px;
    justify-content:center;
    padding: 10px;
}

.skills-item {
    border-radius: 10px;
    padding: 15px;
    width: 290px;
    height: auto;
    margin: 10px;
    background-color: #E4E4E4;
    background-color: var(--white-base);
    color: #3C3B49;
    color: var(--black-base);
    align-items: center;
    text-align: center;
    outline: 2px solid #E4E4E4;
    outline: 2px solid var(--white-base);
    transition: 0.4s;
}

.skills-item:hover {
    background-color: #3C3B49;
    background-color: var(--black-base);
    transition: 0.4s;
    -webkit-transform: translateY(-10px);
            transform: translateY(-10px);
    color :#E4E4E4;
    color :var(--white-base);
    cursor: pointer;
}

.skills-icon {
    width: 50px;
    height: auto;
}

/*ACHIEVEMENTS SECTION*/

.achievements {
    background-image: url(/static/media/3.b9518d77.jpg);
    background-size: cover;
    background-attachment: fixed;
    padding: 55px;
    color: #3C3B49;
    color: var(--black-base);
    text-align: center;
    height: 100vh;
}

.ach-row {
    display: flex;
    flex-direction:row;
    flex-wrap: nowrap;
    justify-content: center;
}

.ach-img {
    height: auto;
    width: 500px;
    margin: 25px;
    border-radius: 7px;
    transition: 0.4s;
}

.ach-img:hover {
    -webkit-transform: scale(1.12);
            transform: scale(1.12);
    border: 2px solid #3C3B49;
    border: 2px solid var(--black-base);
    cursor: pointer;
    transition: 0.4s;
}

/*EDUCATION SECTION*/

.education {
    background-color: rgb(0 0 0 / 55%);
    padding: 50px;
    text-align: center;
    color: #E4E4E4;
    color: var(--white-base);
}

.row-schools {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
}

.school {
    padding: 15px;
    width: 300px;
    margin: 15px auto;
    transition: 0.2s;
    border-radius:10px;
}

.school:hover {
    background-color: rgba(255, 255, 255, 65%);
    border-radius: 10px;
    color: #3C3B49;
    color: var(--black-base);
    cursor: pointer;
}

/*CONTACT SECTION*/

.contact {
    background-image: url(/static/media/4.d6f10f30.jpg);
    background-size: cover;
    padding: 55px;
    text-align: center;
    color: #3C3B49;
    color: var(--black-base);
    font-family:'Quicksand', sans-serif;
}

.contact-wrapper {
    display: flex;
    grid-gap: 0;
    gap: 0;
    justify-content: center;
}

.contact-desc {
    -webkit-backdrop-filter: blur(10px);  
    backdrop-filter: blur(10px); 
    box-shadow: 0px 10px 15px 10px rgb(0 0 0 / 15%);
    background-color: rgb(228 228 228 / 25%); 
    margin: 0;
    height: 80vh;
    width: 300px;
    padding-top:32vh;
    font-weight: 700;
    width: 550px;
    border-top-left-radius: 10px ;
    border-bottom-left-radius: 10px ;
    color: black;
    align-items: center;
}

.contact-card {
    -webkit-backdrop-filter: blur(10px);  
    backdrop-filter: blur(10px); 
    box-shadow: 0px 10px 15px 10px rgb(0 0 0 / 15%);
    background-color: rgb(228 228 228 / 25%); 
    margin: 0;
    height: 80vh;
    width: 300px;
    padding: 25px;
    border-top-right-radius: 15px;
    border-bottom-right-radius: 15px;
    color: black;
    font-weight: 700;
}

.contact-card:hover {
    outline: 2px solid black;
}

.contact-desc:hover {
    outline: 2px solid black;
}

.profile {
    border-radius: 50%;
    height: 110px;
    width: 110px;
}

.email-button {
    width: 70%;
    padding: 15px;
    font-size: 15px;
    font-weight: 600;
    color: black;
    cursor: pointer;
    margin: auto;
    height: 55px;
    text-align:center;
    border: 1px solid #E4E4E4;
    border: 1px solid var(--white-base);
    border-radius: 12px;
    transition: 0.3s ease-in-out;
}

.email-button:hover {
    background-color: #E4E4E4;
    background-color: var(--white-base);
    color: black;
}

.email-link {
    text-decoration: none;
}

.email-btn-img {
    width: 20px;
    height: auto;
}


/*FOOTER SECTION*/
footer {
    box-shadow: 0px 10px 15px 10px rgb(0 0 0 / 15%);
    background-color: #3C3B49;
    background-color: var(--black-base); 
    padding: 25px;
    text-align: center;
    border-top: 2px solid #E4E4E4;
    border-top: 2px solid var(--white-base);
    color: #E4E4E4;
    color: var(--white-base);
    
}

/*RESPONSIVE SECTION*/

@media screen and (max-width: 768px) {
    body, html {
        background-repeat: no-repeat;
        scroll-behavior: smooth;
        -ms-scroll-snap-type: none;
            scroll-snap-type: none;
        margin: 0px;
        padding: 0px;
        overflow-x: hidden;
    }
    .navbar-nav {
        padding: 1px;
    }
    .navbar-brand {
        color: #E4E4E4;
        color: var(--white-base);
        margin-left :1px;
        border: none;
    }
    .sect1 {
        height: 100vh;
        margin: 0px 10px 0px 10px;
        text-align: center;
    }
    .row-who {
        display: block;
        padding: 0px;
    }

    .column-who {
        width: 300px;
        margin: 0 auto;
    }
    .about {
        padding:30px;
    }
    #projects {
        background-image: url(/static/media/2.8120f97f.jpg);
        background-size: cover;
        background-attachment: fixed;
        padding: 10px;
    }
    
    .project {
        box-shadow: 0px 10px 15px 10px rgb(0 0 0 / 15%);
        background-color: rgba(0, 0, 0, 0.25);
        margin: 10px;
        padding: 25px;
        text-align: center;
    }

    .project h1{
        color: #E4E4E4;
        color: var(--white-base);
    }

    .title-black {
        color: #E4E4E4;
        color: var(--white-base);
    }
    
    .row-proj {
        display: inline-block;
        flex-wrap: wrap;
        align-content: center;
    }

    .col-proj {
        margin: 10px auto;
        background-color: #3C3B49;
        background-color: var(--black-base);
        border-radius: 10px;
        padding: 0;
        width: 275px;
        height: 350px;
    }
    
    .img-proj {
        width: 275px;
        height: 145px;
    }

    .main-container {
        background-color: #3C3B49;
        background-color: var(--black-base);
        padding: 20px;
        margin: 10px;
        border-radius: 10px;
        color: #E4E4E4;
        color: var(--white-base);
    }

    .skills-container {
        display: block;
        padding: 10px;
    }
    
    .skills-item {
        border-radius: 10px;
        padding: 15px;
        width: 210px;
        height: auto;
        margin: 25px auto;
        background-color: #E4E4E4;
        background-color: var(--white-base);
        color: #3C3B49;
        color: var(--black-base);
        align-items: center;
        text-align: center;
        outline: 2px solid #E4E4E4;
        outline: 2px solid var(--white-base);
    }

    .ach-row {
        display: block;
    }

    .achievements {
        background-image: url(/static/media/3.b9518d77.jpg);
        background-size: cover;
        background-attachment: fixed;
        padding: 25px;
        color: #3C3B49;
        color: var(--black-base);
        text-align: center;
    }

    .ach-img {
        height: auto;
        width: 280px;
        margin: 25px auto;
        transition: 0.3s;
    }

    .row-schools {
        display: block;;
        justify-content: center;
    }

    .school {
        padding: 12px;
        width: 270px;
        margin: 15px auto;
        transition: ease 0.2s;
    }

    .contact {
        padding: 35px 5px 35px 5px;
        text-align: center;
        color: #E4E4E4;
        color: var(--white-base);
    }

    .contact-wrapper {
        display: flex;
        grid-gap: 20px;
        gap: 20px;
        justify-content: center;
        flex-direction: column;
    }
    
    .contact-desc {
        -webkit-backdrop-filter: blur(10px);  
        backdrop-filter: blur(10px); 
        box-shadow: 0px 10px 15px 10px rgb(0 0 0 / 15%);
        background-color: rgb(228 228 228 / 25%); 
        margin: auto;
        height: 80vh;
        padding:32vh 1vh 1vh 1vh;
        font-weight: 700;
        width: 300px;
        border-radius: 10px;
        color: black;
        align-items: center;
    }
    
    .contact-card {
        -webkit-backdrop-filter: blur(10px);  
        backdrop-filter: blur(10px); 
        box-shadow: 0px 10px 15px 10px rgb(0 0 0 / 15%);
        background-color: rgb(228 228 228 / 25%); 
        margin: auto;
        height: 500px;
        width: 300px;
        padding: 15px;
        border-radius: 10px;
        color: black;
    }

    footer {
        padding: 25px 25px 50px 25px;
    }

}
